.container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 80%;
    margin: 0 auto;
    position: relative;

    @media (min-width: 1920px) {
        width: 1200px;
    }

    @media (max-width: 650px) {
        width: 100%;
        padding-bottom: 60px;
    }
}
